.search-box{
    width: 80%;
    text-align: center;
    margin: 40px auto ;
    height: 70px;
  }
  
  .search-bar {
    min-width: 60%;
    /* height: 40px; */
    padding: 15px;
    border-radius: 10px 0 0 10px;
    outline: none;
    border: none;
    font-size: 20px;
    font-weight: 500;
    background-color: rgba(232, 232, 232, 0.9);
    box-shadow: 0 4px rgba(0,0,0,0.2);
    transition: 0.4s ease;
  }
  
  .search-bar:focus{
    background-color:rgb(205, 205, 205);
  }
  
  .search-box .btn{
    height: 56px;
    width: 100px;
    margin: 0;
    padding: 15px;
    border-radius: 0 10px 10px 0;
  
  }
  

  @media (max-width: 1350px){
    .search-bar {
      min-width: 60%;
      height: 100%;
      padding: 15px;
      font-size: 12px;
    }
    .search-box .btn{
      height: 100%;
      width: auto;
      margin: 0;
      padding: 15px;
      padding-bottom: 19px;
      border-radius: 0 10px 10px 0;
    
    }
  }